* {
  box-sizing: border-box;
  margin-bottom: 0px;
}

.App {
  text-align: center;
  min-height: 100vh;
  position: relative;
  background-color: #5a9ac5cc;
}

.App-logo {
  margin-top: 5%;
  margin-bottom: 10px;
  height: 15vmin;
  pointer-events: none;
  animation: App-logo-spin infinite 45s linear;
}

.App-logo-filler {
  margin-top: 5%;
  margin-bottom: 10px;
  height: 60vmin;
  pointer-events: none;
  animation: App-logo-spin-filler infinite 30s linear;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 45s linear;
  }
}

.App-header {
  background-color: #2a85c5;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

#app-name-h1 {
  margin-bottom: 15px;
  font-size: 36px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Pretty Animation for SVG on main page */
@keyframes App-logo-spin-filler {
  0% {
    transform: rotate(0deg);
    opacity: 0.25;
  }
  50% {
    transform: rotate(180deg);
    opacity: 1;
    filter: invert(10%) sepia(41%) saturate(700%) hue-rotate(95deg)
      brightness(87%) contrast(96%);
  }
  100% {
    transform: rotate(360deg);
    opacity: 0.25;
  }
}

h1 {
  font-size: 1.5rem;
  margin-top: 0px;
  margin-bottom: 0px;
}

h3 {
  display: inline-block;
}

.you-drew {
  margin-top: 10px;
  display: inline-block;
}

/******* Help */
.refresh {
  align-items: center;
  justify-content: center;
  float: left;
  width: 52px;
  height: 52px;
  border: 3px solid black;
  border-bottom-right-radius: 10px;
  box-shadow: 1px 2px;
}

#go-home-div {
  overflow: hidden;
  padding-bottom: 10px;
  margin-right: 7px;
}

.go-home {
  align-items: center;
  justify-content: center;
  float: right;
  width: 90px;
  height: 45px;
  border: 3px solid black;
  border-radius: 10px;
  box-shadow: 1px 2px 2px;
}

.home-h3 {
  text-decoration: none;
  color: black;
  margin-left: auto;
  margin-right: auto;
  width: 95%;
  margin-block-start: 0;
}

.refresh-title.first {
  margin-top: -2px;
}

.refresh-title {
  display: block;
  margin-left: -5px;
  font-size: 16px;
  font-weight: 700;
  line-height: 5px;
}

#help-button {
  font-size: 26px;
  font-weight: 700;
  float: right;
  width: 52px;
  height: 52px;
  border: 3px solid black;
  border-bottom-left-radius: 10px;
  box-shadow: 2px 2px;
}

.daily-help-title {
  margin: 0;
}

p {
  margin-bottom: -10px;
  text-align: justify;
  text-indent: 10px;
  padding-left: 7.5px;
  padding-right: 7.5px;
  padding-bottom: 3px;
  border: 2px solid black;
  border-radius: 10px;
}

.summary {
  width: 90%;
  text-align: justify;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 2em;
}

.secondary {
  color: black;
}

.card-image {
  width: 290px;
  border-radius: 15px;
}

.card-image-preview {
  width: 100px;
  margin-top: 4vh;
}

/***** Homepage */

#homepage-div {
  max-width: 690px;
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 32px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

#hide-description {
  font-size: 20px;
  font-weight: 700;
  margin-top: 25px;
  margin-left: auto;
  margin-right: auto;
  width: 150px;
  height: 45px;
  border-radius: 10px;
  border: 3px solid black;
}

#footer-homepage {
  justify-content: flex-end;
  display: flex;
}

/***** One Card */

#one-card-container {
  padding-bottom: 32px;
}

.card-image-back {
  border-radius: 30px;
  border-bottom-right-radius: 40px;
}

/* #one-card-loading {
} */

.card-image-back {
  width: 290px;
  animation: 2s fadeOut;
  animation-fill-mode: backwards;
}

@keyframes fadeOut {
  0% {
    opacity: 1;
    transform: rotate(0deg);
  }

  25% {
    opacity: 1;
    transform: rotate(0deg);
  }

  35% {
    opacity: 1;
  }
  60% {
    opacity: 0;
  }

  100% {
    opacity: 0;
    transform: rotateY(180deg);
  }
}

#one-card-result {
  visibility: hidden;
  animation: 1s fadeIn;
  animation-fill-mode: forwards;
}

.card-image {
  animation: 3s rotateCard;
}

@keyframes rotateCard {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotateY(360deg);
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    visibility: visible;
    opacity: 1;
  }
}

/***** Three Card */
.card-container {
  width: 92%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 60px;
  padding-bottom: 32px;
}

#card-image-first {
  position: relative;
  width: 81px;
  margin-left: 2vh;
  border-radius: 5px;
  transform: rotate(-10deg);
  z-index: 0;
}

#card-image-second {
  margin-bottom: 0.9vh;
  margin-left: -3vh;
  position: relative;
  width: 82px;
  border-radius: 5px;
  z-index: 0;
}
#card-image-third {
  position: relative;
  margin-left: -3vh;
  margin-bottom: -0.3vh;
  width: 82px;
  border-radius: 5px;
  transform: rotate(10deg);
  z-index: 1;
}

.name-title {
  border-top: 5px solid black;
  padding-top: 0.5em;
}

.name-title-one {
  margin-bottom: 15px;
}

.threecard-summary {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}

.threecard-image {
  width: 250px;
  margin-top: 20px;
}

/***** Four Card */

#four-card-container {
  padding-bottom: 32px;
}

#card-image-arching {
  width: 110px;
  display: block;
  margin-top: 25px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 25px;
  border-radius: 5px;
}

#overarching-title {
  margin-top: -20px;
  margin-bottom: 30px;
}

/* Nav Bar */

.main-nav {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}

.main-nav ul {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0;
  width: 100%;
}
.nav-li {
  list-style: none;
  display: table;
  margin-right: 10px;
  margin-bottom: 15px;
  border: 3px solid black;
  border-radius: 5px;
  background-color: grey;
  width: 50px;
  height: 50px;
  font-size: 14px;
}

.nav-li a {
  display: table-cell;
  vertical-align: middle;
  font-size: 16px;
  font-weight: 700;
  padding: 10px 10px;
  text-decoration: none;
  color: black;
}

/* Footer */

#footer {
  display: inline-block;
  position: absolute;
  left: 0;
  bottom: 0;
  text-decoration: none;
  background-color: #2a85c5;
  color: black;
  font-size: 14px;
  font-weight: 700;
  width: 100%;
  height: 32px;
  margin-left: auto;
  margin-right: auto;
}

#footer-ul {
  display: inline-block;
  padding: 0;
  margin: 7px;
}

.footer-li {
  display: inline-block;
}

.footer-li.first {
  margin-right: 15px;
}

/* For Tablet */
@media screen and (min-width: 700px) {
  .App-logo-filler {
    height: 30vmin;
  }

  #one-card-container {
    width: 72.5%;
    margin-left: auto;
    margin-right: auto;
  }

  #three-card-container {
    width: 72.5%;
    margin-left: auto;
    margin-right: auto;
  }

  /* Buttons */

  .refresh {
    width: 100px;
    height: 75px;
    padding-bottom: 1.4em;
  }

  .refresh h3 {
    font-size: 22px;
    margin-left: 0px;
  }

  #help-button {
    width: 100px;
    height: 75px;
    font-size: 36px;
  }
}

/* For Large Screens */
@media screen and (min-width: 1300px) {
  #one-card-container {
    width: 62.5%;
    margin-left: auto;
    margin-right: auto;
  }

  #three-card-container {
    width: 62.5%;
    margin-left: auto;
    margin-right: auto;
  }

  /* Multiple card sizing*/

  #card-image-arching {
    width: 140px;
  }
  #card-image-first {
    width: 120px;
  }
  #card-image-second {
    width: 120px;
  }
  #card-image-third {
    width: 120px;
  }

  #four-card-container {
    width: 62.5%;
    margin-left: auto;
    margin-right: auto;
  }

  .cards-displayed h4 {
    font-size: 32px;
  }

  #overarching-title {
    font-size: 32px;
  }

  .nav-li a {
    font-size: 24px;
    padding: 20px 20px;
  }
}

/* For Widscreen Desktop */
@media screen and (min-width: 1700px) {
  #one-card-container {
    width: 52%;
    margin-left: auto;
    margin-right: auto;
  }

  #three-card-container {
    width: 52%;
    margin-left: auto;
    margin-right: auto;
  }

  #four-card-container {
    width: 52%;
    margin-left: auto;
    margin-right: auto;
  }

  .nav-li a {
    font-size: 24px;
    padding: 30px 30px;
  }

  .card-container {
    width: 85%;
  }
}
